<template>
  <div>
    <top-bar :secondPage="topBar" />
    <van-panel title="管理员：" v-model="userInfo">
      <van-list>
        <van-cell title="头像" title-class="title_css"
          ><img
            :src="userInfo.picture"
            alt=""
            @click="showPicture(userInfo.id)"
            style="
              width: 50px;
              height: 50px;
              border-radius: 50%;
              float: left;
              margin-left: 100px;
            "
        /></van-cell>
        <van-cell title="用户名" title-class="title_css"
          ><div class="content">：{{ userInfo.username }}</div></van-cell
        >
        <van-cell title="昵称" title-class="title_css"
          ><div class="content">：{{ userInfo.nickname }}</div></van-cell
        >
        <van-cell title="邮箱" title-class="title_css"
          ><div class="content">：{{ userInfo.email }}</div></van-cell
        >
        <van-cell title="寝室楼" title-class="title_css"
          ><div class="content">：{{ userInfo.dormitory }}</div></van-cell
        >
        <van-cell title="状态" title-class="title_css"
          ><div class="content">：{{ userInfo.status }}</div></van-cell
        >
        <van-cell title="民族" title-class="title_css"
          ><div class="content">：{{ userInfo.nation }}</div></van-cell
        >
      </van-list>
    </van-panel>
    <!-- 更新头像的dialog -->
    <van-dialog
      v-model="showUpdatePicture"
      title="更新用户头像"
      show-cancel-button
      @confirm="pictureUpdate"
    >
    <van-uploader :after-read="onRead">
      <div>该系统鸽了，谢谢使用</div>
      <van-icon name="photograph" />
    </van-uploader>
    </van-dialog>
  </div>
</template>

<script>
import { userInfoAPI, updatePictureAPI } from '@/api/api'
import TopBar from '@/components/TopBar.vue'
export default {
  components: { TopBar },
  data () {
    return {
      topBar: '个人信息',
      userInfo: {},
      showUpdatePicture: false,
      pictureSet: ''
    }
  },
  methods: {
    async getUserInfo () {
      const res = await userInfoAPI()
      console.log(res.data)
      this.userInfo = res.data
      this.pictureSet = this.userInfo.picture
      console.log(this.userInfo)
    },
    async pictureUpdate () {
      const res = await updatePictureAPI({
        picture: this.pictureSet
      })
      console.log(res)
    },
    showPicture (item) {
      this.dialogTitle = '修改公告'
      this.params = item
      this.showUpdatePicture = true
    },
    onRead (file) {
      console.log(file)
    }
  },
  created () {
    this.getUserInfo()
  }
}
</script>

  <style>
.title_css {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 50px;
  text-align-last: justify;
  color: gray;
  font-size: 12px;
}
.content {
  float: left;
  margin-left: 50px;
  color: black;
}
</style>
